// 
import TimeLine from './tiemline'

// 
const components = [
  TimeLine
]

// installVueuse
const install = function (Vue) {
  if (!install.installed) {
    components.map(component => Vue.component(component.name, component))
  }
}

// 
if (typeof (window) !== 'undefined' && window.Vue) {
  install(window.Vue)
}

export default {
  // install,Vue.use()
  install,
  TimeLine
}
